import { TipService } from './../global/service/tip.service';
import { CommonService } from './../global/service/common.service';
import { RESTSTATUS } from './../global/constant';
import { NavController, IonicPage } from 'ionic-angular';
import { Component, OnInit } from '@angular/core';
@IonicPage({
    name: 'home'
})
@Component({
    selector: 'app-home-page',
    templateUrl: './home.html'
})
export class HomePageComponent implements OnInit {
    constructor(
        private nav: NavController,
        private commonservice: CommonService,
        private tip: TipService
    ) { }
    ngOnInit() {
        this.getUserInfo();
        // this.nav.setRoot('home');
    }
    getUserInfo() {
        this.tip.showLoading();
        this.commonservice.getUserInfo().subscribe(res => {
            this.tip.closeLoading();
            if (res.status === RESTSTATUS.success) {
                console.log(res.data.userInfo)
            }
        });
    }
    /**
     * 跳转产品查询页面
     * 
     * @memberof HomePageComponent
     */
    inquire(type: string) {
        this.nav.push('inquire');
    }
}


